import { Component, OnInit } from '@angular/core';
import { HeaderTitleService } from 'src/app/services/header-title.service';
import { ApiApplicationService } from 'src/app/services/api-application.service';
import { StorageService } from 'src/app/services/storage.service';
import { ApiCRMService } from 'src/app/services/api-crm.service';

@Component({
  selector: 'app-special-offers',
  templateUrl: './special-offers.page.html',
  styleUrls: ['./special-offers.page.scss'],
})
export class SpecialOffersPage implements OnInit {
  arrOffers = [];
  arrUserData = [];

  constructor(
    private headerTitle: HeaderTitleService,
    private apiApplicationService : ApiApplicationService,
    private storage: StorageService,
    private apiCRMService : ApiCRMService
  ) {
    this.headerTitle.title = "Special Offers";
    this.headerTitle.isBack = true;
  }

  ngOnInit() {
    this.getUserData();    
  }
  doRefresh(event) {
    this.getOffers();
    setTimeout(() => { event.target.complete() }, 2000);
  }

  getOffers(){
    let postData = {"IsActive": true, "IsPromoteToDashboard":true};
    this.apiApplicationService.getPromoCode(JSON.stringify(postData)).then(resp=>{
      this.arrOffers = resp; 
      if(!(this.arrUserData["leadID"] == 0 || this.arrUserData["leadID"] == null)){
        this.getUserOffers();
      }
    })
  }
  getUserOffers(){
    let postData = {"IsActive": true, "leadID": this.arrUserData["leadID"]};
    this.apiApplicationService.getPromoCode(JSON.stringify(postData)).then(resp=>{
      if(resp.length > 0){
        resp.forEach(el => {
          this.arrOffers.push(el);
        });                  
      }      
    })
  }

  getUserData() {
    this.storage.getObject("userdata").then((resp: any) => {
      this.apiCRMService.arrUserData = resp;
      this.arrUserData = resp;      
      this.getOffers();
    })
  }

  
}
